.main-container {
  width: 100%;
  background-color: rgb(199, 194, 194);

  .screenshots {
    display: flex;
    flex-direction: column;

    .screenshot {
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      padding: 50px 50px 0px;
    }

    .screenshot_description {
      padding: 0 50px;
      font-size: 20px;
      font-weight: 500;
    }
  }

  .container-functionality {
    padding: 50px 50px;

    &_title {
      font-size: 25px;
    }

    &_subtitle {
      margin-top: 70px;
      font-size: 20px;
    }

    .list-tools {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      flex-wrap: wrap;
      box-sizing: border-box;
      list-style-type: none;

      .tool-name {
        display: flex;
        align-items: center;
        margin: 20px;
        font-size: 18px;
      }

      li {
        display: flex;
        margin-top: 15px;
      }
    }

    .frame {
      display: flex;
      margin-top: 30px;

      &_imgs {
        display: flex;
        flex-direction: column;

        .img_frame {
          margin-bottom: 20px;
        }
      }

      &_description {
        margin-left: 15px;
        font-size: 18px;
      }
    }
  }

  .developer {
    width: 80%;
    height: 500px;
    margin: 70px auto 70px;
    background-color: rgb(27, 85, 85);

    .developer-title {
      font-size: 40px;
      margin-bottom: 40px;
      text-align: center;
      color: white;
    }

    .photo-developer {
      display: block;
      margin: 0 auto;
      border-radius: 100%;
    }

    .about-developer {
      font-size: 40px;
      margin-top: 40px;
      text-align: center;
      color: white;
    }
  }

  .footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 70px;
    background-color: black;
    color: white;

    .nameAuthor {
      margin-left: 30px;

      .linkRSS {
        margin: 0 10px;
        text-decoration: none;
        color: yellow;
      }
    }

    .contacts {
      display: flex;
      margin-right: 30px;
    }
  }
}
